import React, { useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Avatar,
  Button,
  CssBaseline,
  TextField,
  FormControl,
  FormControlLabel,
  Checkbox,
  FormHelperText,
  Grid,
  Box,
  Typography,
  Container,
} from '@mui/material/';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Link } from 'react-router-dom';
import axios from 'axios';
import styled from '@emotion/styled';



// mui css   important  -      
const FormHelperTexts = styled(FormHelperText)`
  width: 100%;
  padding-left: 16px;
  font-weight: 700 !important;
  color: #d32f2f !important;
`;

const Register = () => {
  const theme = createTheme();
  const [checked, setChecked] = useState(false);
  const [emailError, setEmailError] = useState(false);
  const [passwordState, setPasswordState] = useState(false);
  const [passwordError, setPasswordError] = useState('');
  const [usernameError, setUserNameError] = useState('');
  const navigate = useNavigate();
  const [registerError, setRegisterError] = useState('');
  const username = useRef();
  const email = useRef();
  const password = useRef();
  const currentpassword = useRef();




  //  
  const handleAgree = (event) => {
    setChecked(event.target.checked);
  };

  const onhandlePost = async (data) => {
    const { email, username, password, currentpassword } = data;
    const user = { email, username, password, currentpassword };
  // post
  await axios
      .post('/auth/register', user)
      .then(function (response) {
        console.log(response, '');
        navigate('/login');
      })
      .catch(function (err) {
        console.log(err);
        setRegisterError(' .   .');
      });
  };
  

  // form 
  const handleSubmit = async (e) => {
    e.preventDefault();

    const data = new FormData(e.currentTarget);

    const joinData = {
      email: data.get('email'),
      username: data.get('username'),
      password: data.get('password'),
      currentpassword: data.get('currentpassword'),
    };
    
    const { email, username, password, currentpassword } = joinData;

    //   
    const emailRegex = /([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/;
    if (!emailRegex.test(email)) {
      setEmailError("   .");
    } else {
      setEmailError("");
    }

    //   
  const passwordRegex = /^[a-zA-Z-0-9].{5,25}$/;
  if (!passwordRegex.test(password)) {
    setPasswordState('+  6  !');
  } else {
    setPasswordState('');
  }

  //   
  if (password !== currentpassword) {
    setPasswordError('  .');
  } else {
    setPasswordError('');
  }

  //   
    const usernameRegex = /^[-a-zA-Z]+$/;
    if (!usernameRegex.test(username)) {
      setUserNameError('  .');
    } else {
      setUserNameError('');
    }

    //   
    if (!checked) alert('  .');

    //    
    if (
      emailRegex.test(email) &&
      passwordRegex.test(password) &&
      password === currentpassword &&
      usernameRegex.test(username) &&
      checked
    ) {
      console.log(joinData)
      onhandlePost(joinData);
    } 
  };

  return (
    <ThemeProvider theme={theme}>
    <div className='loginbutton' style={{ display: "flex", justifyContent: 'flex-end', marginRight: "50px"}}>
      

      <Link to='/login' style={{ textDecoration: "none",}}>
                <Button>Login</Button>
      </Link>
    </div>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }} />
          <Typography component="h1" variant="h5">
            
          </Typography>
          <Box component="form" noValidate onSubmit={(e) => handleSubmit(e)} sx={{ mt: 3 }}>
            <FormControl component="fieldset" variant="standard">
              <Grid container spacing={2}>
                <Grid item xs={12}>

                  <TextField
                    required
                    autoFocus
                    fullWidth
                    type="email"
                    id="email"
                    name="email"
                    label="  ( example@example.com )"
                    error={emailError !== '' || false}
                    

                  />
                </Grid>
                <FormHelperTexts>{emailError}</FormHelperTexts>
                <Grid item xs={12}>
                  <TextField
                    required
                    fullWidth
                    type="password"
                    id="password"
                    name="password"
                    label=" (+ 6 )"
                    error={passwordState !== '' || false}
                    
                  />
                </Grid>
                <FormHelperTexts>{passwordState}</FormHelperTexts>
                <Grid item xs={12}>
                  <TextField
                    required
                    fullWidth
                    type="password"
                    id="currentpassword"
                    name="currentpassword"
                    label=" "
                    error={passwordError !== '' || false}
                    
                  />
                </Grid>
                <FormHelperTexts>{passwordError}</FormHelperTexts>
                <Grid item xs={12}>
                  <TextField 
                  required 
                  fullWidth id="username" 
                  name="username" label="" 
                  error={usernameError !== '' || false} />
                </Grid>
                <FormHelperTexts>{usernameError}</FormHelperTexts>
                <Grid item xs={12}>
                  <FormControlLabel
                    control={<Checkbox onChange={handleAgree} color="primary" />}
                    label="  ."
                  />
                </Grid>
              </Grid>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
                size="large"
              >
                
              </Button>
            </FormControl>
            <FormHelperTexts>{registerError}</FormHelperTexts>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
};
export default Register;